import {$qsAll, loadScript} from '../../../../@shared/ts/dom';
import {FeatureFlag} from '../../../../@type/features';
import {type NoncePayload, type PoyntOptions} from '../../../../native-checkout/ts/poynt/types';
import {type Token} from '../../../../native-checkout/ts/poynt/class-poynt-card-payment';
import {type GatewayConfiguration} from '../../models/GatewayConfiguration';
import {Feature, startModalProcessing, stopModalLoading} from '../../reducers/environmentReducer';
import {PaymentConfiguration, registerGatewayBatch} from '../../reducers/paymentConfigurationReducer';
import {PeachPayCustomer} from '../../reducers/peachPayCustomerReducer';
import {store} from '../../store';
import {displayPaymentErrorMessage, type OrderService} from '../order';
import {setupPoyntButton} from './button';
import {initPoyntCardMethod} from './card';
import {getErrorString} from '../../../../@shared/ts/error';
import {getLocaleText} from '../../util/translation';
import {PeachPayOrder} from '../../reducers/peachPayOrderReducer';

/**
 * Implementation type for a GoDaddy Poynt gateway
 */
export interface PoyntGateway {
	config: GatewayConfiguration;
	init?: (options: PoyntOptions) => void;
	tokenize: (payload: NoncePayload) => Promise<Token>;
}

export default async function initPoyntPaymentIntegration(orderService: OrderService) {
	if (!Feature.enabled(FeatureFlag.POYNT_GATEWAYS)) {
		return;
	}

	const poyntSrc = Feature.metadata<string>(FeatureFlag.POYNT_GATEWAYS, 'poynt_src');
	const businessId = Feature.metadata<string>(FeatureFlag.POYNT_GATEWAYS, 'business_id');
	const applicationId = Feature.metadata<string>(FeatureFlag.POYNT_GATEWAYS, 'application_id');

	if (!businessId || !applicationId || !poyntSrc) {
		return;
	}

	const poyntOptions = {businessId, applicationId};

	await loadScript(poyntSrc, null, () => {
		const gateways = registerPoyntGateways();
		activatePoyntGateways(gateways, orderService, poyntOptions);
		setupPoyntButton();
	});
}

function registerPoyntGateways(): Record<string, PoyntGateway> {
	const gatewayImplementations: Record<string, PoyntGateway> = {};
	const gatewayConfigurations: Record<string, GatewayConfiguration> = {};

	if (Feature.enabled(FeatureFlag.POYNT_CARD_GATEWAY)) {
		const method = initPoyntCardMethod();
		gatewayConfigurations[method.config.gatewayId] = method.config;
		gatewayImplementations[method.config.gatewayId] = method;
	}

	store.dispatch(registerGatewayBatch(gatewayConfigurations));

	return gatewayImplementations;
}

function activatePoyntGateways(gateways: Record<string, PoyntGateway>, orderService: OrderService, options: PoyntOptions) {
	for (const [_, gateway] of Object.entries(gateways)) {
		if (gateway.init) {
			gateway.init(options);
		}
	}

	const confirm = async (event: MouseEvent) => {
		if (!await PeachPayOrder.reportValidity()) {
			return;
		}

		const $target = event.target as HTMLElement | null;
		const $button = $target?.closest<HTMLButtonElement>('button');
		if (!$button) {
			return;
		}

		const gatewayId = PaymentConfiguration.selectedGateway();
		const gateway = gateways[gatewayId];
		if (!gateway) {
			return;
		}

		return poyntPaymentFlow(gateway, orderService);
	};

	$qsAll<HTMLElement>('.poynt-btn', $el => {
		$el.addEventListener('click', async event => confirm(event));
	});
}

async function poyntPaymentFlow(payment: PoyntGateway, orderService: OrderService) {
	$qsAll('.pp-pm-error-text', $el => {
		$el.remove();
	});

	store.dispatch(startModalProcessing());

	const payload = getNoncePayload();

	const {nonce, error} = await payment.tokenize(payload);

	const {error: transactionError, result: transaction} = await orderService.startTransaction(payment.config.gatewayId);
	if (transactionError || !transaction) {
		const errorMessage = transactionError ? getErrorString(transactionError) : getLocaleText('An unknown error occured while starting the transaction. Please refresh the page and try again.');
		displayPaymentErrorMessage(errorMessage);

		store.dispatch(stopModalLoading());
		return;
	}

	if (error ?? !nonce) {
		await transaction.complete({
			paymentStatus: 'creationfailed',
			orderStatus: 'unperformed',
			note: error ?? 'An unknown error occured while retrieving the nonce.',
		});

		displayPaymentErrorMessage(error ?? 'An unknown error occured while tokenizing the payment method.');
		store.dispatch(stopModalLoading());
		return;
	}

	const {error: orderError, result: orderResult} = await orderService.placeOrder(transaction, {
		peachpay_poynt_nonce: nonce,
	});

	if (orderError || !orderResult || orderResult.result !== 'success') {
		store.dispatch(stopModalLoading());
		return;
	}

	if (window.top) {
		await transaction.complete();
		window.top.location = orderResult.redirect;
	}
}

function getNoncePayload(): NoncePayload {
	const payload = {} as NoncePayload;
	if (PeachPayCustomer.billing.email()) {
		payload.emailAddress = PeachPayCustomer.billing.email();
	}

	if (PeachPayCustomer.billing.firstName()) {
		payload.firstName = PeachPayCustomer.billing.firstName();
	}

	if (PeachPayCustomer.billing.lastName()) {
		payload.lastName = PeachPayCustomer.billing.lastName();
	}

	if (PeachPayCustomer.billing.address1()) {
		payload.line1 = PeachPayCustomer.billing.address1();
	}

	if (PeachPayCustomer.billing.postal()) {
		payload.zipCode = PeachPayCustomer.billing.postal();
	}

	return payload;
}
